.counter-interface {
  background: linear-gradient(135deg, #27ae60, #2ecc71);
  color: white;
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.counter-interface.loading {
  display: flex;
  align-items: center;
  justify-content: center;
}

.loading-spinner {
  text-align: center;
  font-size: 1.5rem;
}

.loading-spinner i {
  font-size: 3rem;
  margin-bottom: 1rem;
  display: block;
}

.counter-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background: rgba(255, 255, 255, 0.1);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.counter-info {
  flex: 1;
}

.counter-number {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 5px;
}

.counter-department {
  font-size: 1.2rem;
  opacity: 0.9;
  margin-bottom: 5px;
}

.counter-status {
  font-size: 0.9rem;
  opacity: 0.8;
}

.status-text {
  font-weight: bold;
  text-transform: uppercase;
}

.status-text.active {
  color: #2ecc71;
}

.status-text.busy {
  color: #e74c3c;
}

.status-text.offline {
  color: #95a5a6;
}

.status-indicator {
  padding: 10px 20px;
  border-radius: 25px;
  font-size: 1.1rem;
  font-weight: bold;
  text-transform: uppercase;
}

.status-indicator.active {
  background: #2ecc71;
  color: white;
}

.status-indicator.busy {
  background: #e74c3c;
  color: white;
}

.status-indicator.offline {
  background: #95a5a6;
  color: white;
}

.current-patient {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px;
  text-align: center;
}

.ticket-display-large {
  animation: pulse 2s infinite;
}

.ticket-number {
  font-size: 8rem;
  font-weight: bold;
  color: #f39c12;
  margin-bottom: 20px;
  line-height: 1;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.patient-info {
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 20px;
}

.info-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  font-size: 1.2rem;
}

.label {
  font-weight: 500;
  opacity: 0.9;
}

.value {
  font-weight: bold;
}

.priority-badge {
  padding: 5px 10px;
  border-radius: 15px;
  font-size: 0.9rem;
  font-weight: bold;
  color: white;
}

.call-time {
  font-size: 1.1rem;
  opacity: 0.9;
  display: flex;
  align-items: center;
  gap: 8px;
}

.no-patient {
  padding: 40px;
}

.placeholder {
  font-size: 6rem;
  color: rgba(255, 255, 255, 0.3);
  margin-bottom: 20px;
}

.no-patient-text {
  font-size: 1.5rem;
  margin-bottom: 10px;
  opacity: 0.9;
}

.queue-status {
  font-size: 1.1rem;
  opacity: 0.8;
}

.counter-controls {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 15px;
  padding: 20px;
  background: rgba(255, 255, 255, 0.1);
}

.control-btn {
  background: white;
  border: none;
  padding: 15px;
  border-radius: 10px;
  font-size: 1.1rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  min-height: 60px;
}

.control-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.control-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none !important;
}

.control-btn.previous {
  background: #3498db;
  color: white;
}

.control-btn.call-next {
  background: #e74c3c;
  color: white;
}

.control-btn.recall {
  background: #f39c12;
  color: white;
}

.control-btn.complete {
  background: #27ae60;
  color: white;
}

.control-btn.refresh {
  background: #9b59b6;
  color: white;
}

.queue-panel {
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}

.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.panel-header h4 {
  margin: 0;
  font-size: 1.3rem;
}

.queue-count {
  background: rgba(255, 255, 255, 0.2);
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 0.9rem;
}

.queue-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  max-height: 200px;
  overflow-y: auto;
}

.queue-item {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 12px 15px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  transition: all 0.3s ease;
}

.queue-item:hover {
  background: rgba(255, 255, 255, 0.2);
}

.queue-position {
  font-size: 0.9rem;
  opacity: 0.8;
  min-width: 30px;
}

.ticket-info {
  flex: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.ticket-num {
  font-size: 1.3rem;
  font-weight: bold;
}

.ticket-priority {
  font-size: 0.8rem;
  font-weight: bold;
  text-transform: uppercase;
}

.patient-name {
  font-size: 0.9rem;
  opacity: 0.8;
  font-style: italic;
}

.empty-queue {
  text-align: center;
  padding: 20px;
  opacity: 0.7;
  font-style: italic;
}

.empty-queue i {
  font-size: 1.5rem;
  margin-bottom: 8px;
  display: block;
  color: #2ecc71;
}

.info-panels {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  padding: 20px;
  flex: 1;
  overflow: hidden;
}

.call-history, .activity-panel {
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
}

.call-history h4, .activity-panel h4 {
  margin: 0 0 15px 0;
  font-size: 1.2rem;
}

.history-list, .activity-list {
  flex: 1;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.history-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 6px;
}

.history-number {
  font-weight: bold;
  font-size: 1.1rem;
}

.history-time {
  font-size: 0.8rem;
  opacity: 0.8;
}

.activity-item {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 8px 10px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  font-size: 0.9rem;
}

.activity-time {
  font-size: 0.8rem;
  opacity: 0.8;
  min-width: 50px;
}

.activity-message {
  flex: 1;
}

.activity-icon {
  font-size: 0.9rem;
}

.activity-icon.success {
  color: #2ecc71;
}

.activity-icon.error {
  color: #e74c3c;
}

.activity-icon.warning {
  color: #f39c12;
}

.activity-icon.info {
  color: #3498db;
}

.no-activity {
  text-align: center;
  padding: 20px;
  opacity: 0.7;
  font-style: italic;
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.02); }
  100% { transform: scale(1); }
}

/* Responsive Design */
@media (max-width: 768px) {
  .counter-controls {
    grid-template-columns: repeat(3, 1fr);
  }
  
  .info-panels {
    grid-template-columns: 1fr;
  }
  
  .ticket-number {
    font-size: 5rem;
  }
  
  .counter-number {
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  .counter-controls {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .control-btn {
    font-size: 1rem;
    padding: 12px;
  }
  
  .ticket-number {
    font-size: 4rem;
  }
}