.department-display {
  background: linear-gradient(135deg, #27ae60, #2ecc71);
  color: white;
  min-height: 100vh;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.dept-header {
  padding: 20px;
  background: rgba(255, 255, 255, 0.1);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.dept-branding {
  display: flex;
  align-items: center;
  gap: 20px;
}

.dept-branding i {
  font-size: 3rem;
  color: #f39c12;
}

.dept-info h1 {
  margin: 0;
  font-size: 2.5rem;
}

.dept-info p {
  margin: 5px 0 0 0;
  opacity: 0.9;
  font-size: 1.2rem;
}

.dept-content {
  padding: 40px;
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 30px;
  max-width: 1200px;
  margin: 0 auto;
  align-items: start;
}

.current-serving {
  text-align: center;
  padding: 30px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 20px;
}

.serving-title {
  color: #f39c12;
  font-size: 2.5rem;
  margin-bottom: 30px;
}

.serving-ticket-large {
  animation: pulse 2s infinite;
}

.ticket-number-xlarge {
  font-size: 8rem;
  font-weight: bold;
  color: #f39c12;
  margin-bottom: 20px;
  line-height: 1;
}

.ticket-counter {
  font-size: 2rem;
  margin-bottom: 15px;
}

.patient-name {
  font-size: 1.5rem;
  opacity: 0.9;
}

.no-serving {
  padding: 40px;
}

.placeholder-xlarge {
  font-size: 6rem;
  color: #bdc3c7;
  margin-bottom: 20px;
}

.upcoming-queue {
  background: rgba(255, 255, 255, 0.1);
  padding: 25px;
  border-radius: 15px;
}

.queue-title {
  color: #f39c12;
  margin-bottom: 20px;
  font-size: 1.5rem;
  text-align: center;
}

.queue-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.queue-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  font-size: 1.2rem;
}

.queue-number {
  font-weight: bold;
}

.queue-time {
  opacity: 0.8;
  font-size: 1rem;
}

.empty-queue {
  text-align: center;
  padding: 20px;
  opacity: 0.7;
}

.dept-stats {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
  margin-top: 20px;
}

.stat-card {
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}

.stat-value {
  font-size: 2.5rem;
  font-weight: bold;
  color: #f39c12;
  margin-bottom: 5px;
}

.stat-label {
  font-size: 1rem;
  opacity: 0.8;
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.02); }
  100% { transform: scale(1); }
}