.reception-display {
  background: linear-gradient(135deg, #1a5276, #2980b9);
  color: white;
  min-height: 100vh;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.reception-display.default { background: linear-gradient(135deg, #1a5276, #2980b9); }
.reception-display.green { background: linear-gradient(135deg, #27ae60, #2ecc71); }
.reception-display.red { background: linear-gradient(135deg, #e74c3c, #c0392b); }
.reception-display.dark { background: linear-gradient(135deg, #2c3e50, #34495e); }
.reception-display.professional { background: linear-gradient(135deg, #34495e, #2c3e50); }

.display-header {
  padding: 20px;
  background: rgba(255, 255, 255, 0.1);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.hospital-branding {
  display: flex;
  align-items: center;
  gap: 20px;
}

.hospital-branding i {
  font-size: 3rem;
  color: #f39c12;
}

.hospital-info h1 {
  margin: 0;
  font-size: 2.5rem;
}

.hospital-info p {
  margin: 5px 0 0 0;
  opacity: 0.9;
  font-size: 1.2rem;
}

.reception-content {
  padding: 30px;
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 30px;
  max-width: 1400px;
  margin: 0 auto;
}

.section-title {
  color: #f39c12;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 2rem;
}

.now-serving-section {
  grid-column: 1;
}

.current-tickets {
  display: grid;
  gap: 15px;
}

.serving-ticket {
  background: rgba(255, 255, 255, 0.1);
  padding: 25px;
  border-radius: 15px;
  border: 3px solid #e74c3c;
  text-align: center;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.02); }
  100% { transform: scale(1); }
}

.serving-ticket .ticket-number {
  font-size: 4rem;
  font-weight: bold;
  color: #f39c12;
  margin-bottom: 10px;
}

.ticket-details {
  font-size: 1.3rem;
}

.department {
  font-weight: bold;
  margin-bottom: 5px;
}

.counter {
  color: #bdc3c7;
}

.no-active-calls {
  background: rgba(255, 255, 255, 0.1);
  padding: 40px;
  border-radius: 15px;
  text-align: center;
}

.placeholder {
  font-size: 3rem;
  color: #bdc3c7;
  margin-bottom: 10px;
}

.subtext {
  font-size: 1.2rem;
  opacity: 0.8;
}

.queues-section {
  grid-column: 2;
}

.queues-grid {
  display: grid;
  gap: 15px;
}

.queue-card {
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  transition: all 0.3s ease;
}

.queue-card:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
}

.dept-name {
  font-size: 1.1rem;
  margin-bottom: 10px;
  font-weight: 500;
}

.waiting-count {
  font-size: 2.5rem;
  font-weight: bold;
  color: #f39c12;
  margin-bottom: 5px;
}

.waiting-text {
  font-size: 0.9rem;
  opacity: 0.8;
}

.advertisement-section {
  grid-column: 1 / -1;
  background: rgba(231, 76, 60, 0.8);
  padding: 15px;
  border-radius: 8px;
  text-align: center;
  margin-top: 20px;
}

.ad-content {
  font-size: 1.3rem;
  font-weight: 500;
}

.custom-message {
  grid-column: 1 / -1;
  background: rgba(52, 152, 219, 0.8);
  padding: 15px;
  border-radius: 8px;
  text-align: center;
  margin-top: 15px;
  font-style: italic;
}