.counter-popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  padding: 20px;
}

.counter-popup {
  background: linear-gradient(135deg, #1a5276, #2980b9);
  border-radius: 20px;
  padding: 30px;
  width: 90%;
  max-width: 600px;
  max-height: 90vh;
  overflow-y: auto;
  color: white;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.popup-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 25px;
  padding-bottom: 15px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.popup-title h2 {
  margin: 0 0 5px 0;
  font-size: 1.8em;
}

.department {
  opacity: 0.8;
  font-size: 1.1em;
}

.close-btn {
  background: rgba(255, 255, 255, 0.2);
  border: none;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 1.2em;
  transition: all 0.3s ease;
}

.close-btn:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: rotate(90deg);
}

.current-status-section {
  text-align: center;
  margin-bottom: 25px;
  padding: 20px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  backdrop-filter: blur(10px);
}

.status-indicator.large {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
}

.status-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.status-dot.active { background: #27ae60; }
.status-dot.busy { background: #e74c3c; }
.status-dot.break { background: #f39c12; }
.status-dot.offline { background: #95a5a6; }

.status-text {
  font-size: 1.2em;
  font-weight: bold;
}

.tokens-display-large {
  display: flex;
  justify-content: space-around;
  margin: 25px 0;
}

.token-display {
  text-align: center;
}

.token-label {
  font-size: 0.9em;
  opacity: 0.8;
  margin-bottom: 8px;
}

.token-number-large {
  font-size: 2.5em;
  font-weight: bold;
}

.token-number-large.next {
  color: #2ecc71;
}

.current-patient-info {
  margin-top: 15px;
  padding-top: 15px;
  border-top: 1px solid rgba(255, 255, 255, 0.3);
}

.patient-name {
  font-weight: bold;
  margin-bottom: 5px;
}

.call-time {
  opacity: 0.8;
  font-size: 0.9em;
}

.popup-controls {
  display: flex;
  gap: 10px;
  margin-bottom: 25px;
  flex-wrap: wrap;
}

.control-btn {
  flex: 1;
  min-width: 120px;
  padding: 12px 20px;
  border: none;
  border-radius: 10px;
  font-size: 1em;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.control-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none !important;
}

.control-btn.call-next {
  background: #27ae60;
  color: white;
}

.control-btn.call-next:hover:not(:disabled) {
  background: #2ecc71;
  transform: translateY(-2px);
}

.control-btn.recall {
  background: #f39c12;
  color: white;
}

.control-btn.recall:hover:not(:disabled) {
  background: #f1c40f;
  transform: translateY(-2px);
}

.control-btn.complete {
  background: #3498db;
  color: white;
}

.control-btn.complete:hover:not(:disabled) {
  background: #2980b9;
  transform: translateY(-2px);
}

.queue-section h3 {
  margin-bottom: 15px;
  font-size: 1.3em;
  text-align: center;
}

.queue-list {
  max-height: 300px;
  overflow-y: auto;
}

.queue-item {
  display: flex;
  align-items: center;
  padding: 12px 15px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  margin-bottom: 8px;
  transition: all 0.3s ease;
}

.queue-item:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateX(5px);
}

.queue-position {
  background: #f39c12;
  color: white;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  margin-right: 15px;
  flex-shrink: 0;
}

.ticket-info {
  flex: 1;
}

.ticket-number {
  font-weight: bold;
  font-size: 1.1em;
  margin-bottom: 3px;
}

.patient-name-small {
  opacity: 0.8;
  font-size: 0.9em;
}

.wait-time {
  opacity: 0.7;
  font-size: 0.8em;
  flex-shrink: 0;
}

.empty-queue {
  text-align: center;
  padding: 30px;
  opacity: 0.7;
}

.empty-queue i {
  font-size: 2em;
  margin-bottom: 10px;
  display: block;
}

/* Scrollbar Styling */
.queue-list::-webkit-scrollbar {
  width: 6px;
}

.queue-list::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
}

.queue-list::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.3);
  border-radius: 3px;
}

.queue-list::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.5);
}

/* Responsive Design */
@media (max-width: 768px) {
  .counter-popup {
    padding: 20px;
    width: 95%;
  }
  
  .popup-header {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  
  .close-btn {
    margin-top: 15px;
  }
  
  .tokens-display-large {
    flex-direction: column;
    gap: 20px;
  }
  
  .popup-controls {
    flex-direction: column;
  }
  
  .control-btn {
    min-width: auto;
  }
}

@media (max-width: 480px) {
  .counter-popup {
    padding: 15px;
  }
  
  .popup-title h2 {
    font-size: 1.5em;
  }
  
  .token-number-large {
    font-size: 2em;
  }
  
  .queue-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }
  
  .queue-position {
    margin-right: 0;
  }
}