/* AdminDashboard.css - Complete Enhanced Version */

:root {
  --primary-color: #10a37f;
  --primary-dark: #0d8c6c;
  --secondary-color: #5436da;
  --background-color: #f8f9fa;
  --sidebar-bg: #ffffff;
  --card-bg: #ffffff;
  --text-primary: #1a1a1a;
  --text-secondary: #6b7280;
  --text-light: #9ca3af;
  --border-color: #e5e7eb;
  --success-color: #10b981;
  --warning-color: #f59e0b;
  --danger-color: #ef4444;
  --info-color: #3b82f6;
  --shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  background-color: var(--background-color);
  color: var(--text-primary);
  line-height: 1.5;
}

.admin-dashboard {
  display: flex;
  min-height: 100vh;
}

/* Sidebar Styles */
.sidebar {
  width: 280px;
  background: var(--sidebar-bg);
  border-right: 1px solid var(--border-color);
  display: flex;
  flex-direction: column;
  transition: width 0.3s ease;
  box-shadow: var(--shadow);
  z-index: 10;
}

.sidebar.collapsed {
  width: 70px;
}

.sidebar-header {
  padding: 20px;
  border-bottom: 1px solid var(--border-color);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  display: flex;
  align-items: center;
  gap: 12px;
}

.logo-icon {
  width: 36px;
  height: 36px;
  background: var(--primary-color);
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 18px;
}

.logo-text h1 {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.logo-text p {
  font-size: 12px;
  color: var(--text-secondary);
  margin: 0;
}

.sidebar-toggle {
  background: none;
  border: none;
  color: var(--text-secondary);
  cursor: pointer;
  padding: 8px;
  border-radius: 6px;
  transition: all 0.2s;
}

.sidebar-toggle:hover {
  background: var(--background-color);
  color: var(--text-primary);
}

.menu {
  flex: 1;
  padding: 16px 0;
}

.menu-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 20px;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.2s;
  font-weight: 500;
}

.menu-item:hover {
  background: var(--background-color);
  color: var(--text-primary);
}

.menu-item.active {
  background: rgba(16, 163, 127, 0.1);
  color: var(--primary-color);
  border-right: 3px solid var(--primary-color);
}

.menu-item i {
  width: 20px;
  text-align: center;
}

.sidebar.collapsed .logo-text,
.sidebar.collapsed .menu-item span {
  display: none;
}

.system-controls {
  padding: 20px;
  border-top: 1px solid var(--border-color);
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.btn-system {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px 16px;
  background: var(--background-color);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  color: var(--text-primary);
  cursor: pointer;
  transition: all 0.2s;
  font-weight: 500;
}

.btn-system:hover {
  background: #f1f5f9;
  border-color: var(--text-light);
}

.btn-system.btn-danger {
  color: var(--danger-color);
  border-color: rgba(239, 68, 68, 0.2);
}

.btn-system.btn-danger:hover {
  background: rgba(239, 68, 68, 0.05);
}

/* Main Content Styles */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 24px;
  background: white;
  border-bottom: 1px solid var(--border-color);
  box-shadow: var(--shadow);
}

.header-left h2 {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 4px;
}

.breadcrumb {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  color: var(--text-secondary);
}

.breadcrumb .current-page {
  color: var(--text-primary);
  font-weight: 500;
}

.breadcrumb i {
  font-size: 12px;
}

.header-controls {
  display: flex;
  align-items: center;
  gap: 12px;
}

.btn-icon {
  position: relative;
  background: none;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.2s;
}

.btn-icon:hover {
  background: var(--background-color);
  color: var(--text-primary);
}

/* User Dropdown Styles */
.user-info-container {
  position: relative;
}

.user-info {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 8px 12px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
  position: relative;
}

.user-info:hover {
  background: var(--background-color);
}

.user-info .fa-chevron-down {
  transition: transform 0.2s ease;
  font-size: 12px;
}

.user-info .fa-chevron-down.rotate {
  transform: rotate(180deg);
}

.user-avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  overflow: hidden;
  background: var(--primary-color);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 600;
}

.user-avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.user-details {
  display: flex;
  flex-direction: column;
}

.user-name {
  font-weight: 600;
  font-size: 14px;
}

.user-role {
  font-size: 12px;
  color: var(--text-secondary);
}

.user-dropdown {
  position: absolute;
  top: 100%;
  right: 0;
  width: 200px;
  background: white;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  box-shadow: var(--shadow-lg);
  z-index: 1000;
  margin-top: 8px;
  overflow: hidden;
}

.dropdown-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 16px;
  cursor: pointer;
  transition: all 0.2s;
  color: var(--text-primary);
  text-decoration: none;
  border: none;
  background: none;
  width: 100%;
  text-align: left;
  font-size: 14px;
}

.dropdown-item:hover {
  background: var(--background-color);
}

.dropdown-item.logout {
  color: var(--danger-color);
}

.dropdown-item.logout:hover {
  background: rgba(239, 68, 68, 0.05);
}

.dropdown-divider {
  height: 1px;
  background: var(--border-color);
  margin: 4px 0;
}

/* Click outside to close dropdown */
.user-dropdown-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
}

/* Remove notification badge styles */
.notification-badge {
  display: none;
}

.content-area {
  flex: 1;
  padding: 24px;
  overflow-y: auto;
}

/* Dashboard Tab Styles */
.dashboard-loading {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 400px;
}

.loading-spinner {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  color: var(--text-secondary);
}

.spinner {
  width: 40px;
  height: 40px;
  border: 4px solid rgba(16, 163, 127, 0.2);
  border-left: 4px solid var(--primary-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.dashboard-tab {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 20px;
}

.stat-card {
  background: var(--card-bg);
  border-radius: 12px;
  padding: 20px;
  box-shadow: var(--shadow);
  display: flex;
  align-items: center;
  gap: 16px;
  transition: transform 0.2s, box-shadow 0.2s;
}

.stat-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.stat-card.primary {
  border-left: 4px solid var(--primary-color);
}

.stat-card.success {
  border-left: 4px solid var(--success-color);
}

.stat-card.warning {
  border-left: 4px solid var(--warning-color);
}

.stat-card.info {
  border-left: 4px solid var(--info-color);
}

.stat-icon {
  width: 48px;
  height: 48px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
}

.stat-card.primary .stat-icon {
  background: rgba(16, 163, 127, 0.1);
  color: var(--primary-color);
}

.stat-card.success .stat-icon {
  background: rgba(16, 185, 129, 0.1);
  color: var(--success-color);
}

.stat-card.warning .stat-icon {
  background: rgba(245, 158, 11, 0.1);
  color: var(--warning-color);
}

.stat-card.info .stat-icon {
  background: rgba(59, 130, 246, 0.1);
  color: var(--info-color);
}

.stat-value {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 4px;
}

.stat-value .unit {
  font-size: 16px;
  font-weight: 500;
  color: var(--text-secondary);
}

.stat-label {
  font-size: 14px;
  color: var(--text-secondary);
  margin-bottom: 8px;
}

.stat-trend {
  display: flex;
  align-items: center;
  gap: 4px;
  font-size: 12px;
}

.stat-trend.positive {
  color: var(--success-color);
}

.stat-trend.negative {
  color: var(--danger-color);
}

.dashboard-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 24px;
}

.dashboard-section {
  background: var(--card-bg);
  border-radius: 12px;
  padding: 24px;
  box-shadow: var(--shadow);
}

.dashboard-section.full-width {
  grid-column: 1 / -1;
}

.section-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}

.section-header h3 {
  font-size: 18px;
  font-weight: 600;
}

.btn-text {
  background: none;
  border: none;
  color: var(--primary-color);
  font-weight: 500;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 14px;
  transition: all 0.2s;
}

.btn-text:hover {
  color: var(--primary-dark);
}

/* Queue Cards */
.queue-cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 16px;
}

.queue-card {
  background: var(--background-color);
  border-radius: 10px;
  padding: 16px;
  transition: all 0.2s;
  border: 1px solid var(--border-color);
}

.queue-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow);
}

.queue-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 12px;
}

.queue-header h4 {
  font-size: 14px;
  font-weight: 600;
  margin: 0;
}

.queue-status {
  font-size: 10px;
  padding: 4px 8px;
  border-radius: 20px;
  font-weight: 600;
}

.queue-status.high {
  background: rgba(239, 68, 68, 0.1);
  color: var(--danger-color);
}

.queue-status.medium {
  background: rgba(245, 158, 11, 0.1);
  color: var(--warning-color);
}

.queue-status.low {
  background: rgba(16, 185, 129, 0.1);
  color: var(--success-color);
}

.queue-count {
  font-size: 32px;
  font-weight: 700;
  margin-bottom: 8px;
}

.queue-wait {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 12px;
  color: var(--text-secondary);
  margin-bottom: 12px;
}

.queue-progress {
  margin-top: 8px;
}

.progress-bar {
  height: 6px;
  background: #e5e7eb;
  border-radius: 3px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background: var(--primary-color);
  border-radius: 3px;
  transition: width 0.3s ease;
}

/* Counters Grid */
.counters-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  gap: 16px;
}

.counter-item {
  background: var(--background-color);
  border-radius: 10px;
  padding: 16px;
  border: 1px solid var(--border-color);
  transition: all 0.2s;
}

.counter-item:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow);
}

.counter-item.active {
  border-color: var(--success-color);
}

.counter-item.idle {
  border-color: var(--warning-color);
}

.counter-item.offline {
  border-color: var(--text-light);
}

.counter-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 8px;
}

.counter-number {
  font-weight: 600;
  font-size: 14px;
}

.status-indicator {
  width: 10px;
  height: 10px;
  border-radius: 50%;
}

.status-indicator.active {
  background: var(--success-color);
}

.status-indicator.idle {
  background: var(--warning-color);
}

.status-indicator.offline {
  background: var(--text-light);
}

.counter-department {
  font-size: 12px;
  color: var(--text-secondary);
  margin-bottom: 12px;
}

.current-ticket {
  background: white;
  padding: 8px;
  border-radius: 6px;
  margin-bottom: 12px;
  text-align: center;
  border: 1px solid var(--border-color);
}

.ticket-number {
  font-weight: 700;
  font-size: 16px;
  color: var(--primary-color);
}

.ticket-time {
  font-size: 10px;
  color: var(--text-secondary);
}

.no-ticket {
  background: white;
  padding: 8px;
  border-radius: 6px;
  margin-bottom: 12px;
  text-align: center;
  border: 1px dashed var(--border-color);
  color: var(--text-light);
  font-size: 12px;
}

.counter-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.status-text {
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
}

.status-text.active {
  color: var(--success-color);
}

.status-text.idle {
  color: var(--warning-color);
}

.status-text.offline {
  color: var(--text-light);
}

/* Activity List */
.activity-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.activity-item {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  padding: 12px;
  border-radius: 8px;
  transition: all 0.2s;
}

.activity-item:hover {
  background: var(--background-color);
}

.activity-icon {
  width: 32px;
  height: 32px;
  border-radius: 8px;
  background: rgba(16, 163, 127, 0.1);
  color: var(--primary-color);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  flex-shrink: 0;
}

.activity-content {
  flex: 1;
}

.activity-message {
  font-size: 14px;
  margin-bottom: 4px;
}

.activity-time {
  font-size: 12px;
  color: var(--text-secondary);
}

.no-activities {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
  color: var(--text-light);
}

.no-activities i {
  font-size: 40px;
  margin-bottom: 16px;
  opacity: 0.5;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .dashboard-content {
    grid-template-columns: 1fr;
  }
  
  .stats-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .admin-dashboard {
    flex-direction: column;
  }
  
  .sidebar {
    width: 100%;
    height: auto;
  }
  
  .sidebar.collapsed {
    width: 100%;
  }
  
  .menu {
    display: flex;
    overflow-x: auto;
    padding: 16px;
  }
  
  .menu-item {
    white-space: nowrap;
  }
  
  .stats-grid {
    grid-template-columns: 1fr;
  }
  
  .queue-cards,
  .counters-grid {
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  }
  
  .header {
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
  }
  
  .header-controls {
    width: 100%;
    justify-content: space-between;
  }
  
  .user-dropdown {
    position: fixed;
    top: auto;
    right: 16px;
    left: 16px;
    width: auto;
  }
}

@media (max-width: 480px) {
  .content-area {
    padding: 16px;
  }
  
  .header {
    padding: 12px 16px;
  }
  
  .header-left h2 {
    font-size: 20px;
  }
  
  .dashboard-section {
    padding: 16px;
  }
  
  .stats-grid {
    gap: 12px;
  }
  
  .stat-card {
    padding: 16px;
  }
  
  .stat-value {
    font-size: 24px;
  }
  
  .user-details {
    display: none;
  }
}