/* QueueControl.css - Professional Enhanced Version */

:root {
  --primary-color: #10a37f;
  --primary-dark: #0d8c6c;
  --secondary-color: #6b7280;
  --success-color: #10b981;
  --warning-color: #f59e0b;
  --error-color: #ef4444;
  --info-color: #3b82f6;
  --background-color: #f8fafc;
  --card-bg: #ffffff;
  --border-color: #e2e8f0;
  --text-primary: #1e293b;
  --text-secondary: #64748b;
  --text-light: #94a3b8;
  --shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.queue-control {
  min-height: 100vh;
  background: var(--background-color);
  padding: 0;
}

/* Loading State */
.queue-control-loading {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 400px;
}

.loading-spinner {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  color: var(--text-secondary);
}

.spinner {
  width: 40px;
  height: 40px;
  border: 4px solid rgba(16, 163, 127, 0.2);
  border-left: 4px solid var(--primary-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Header Section */
.control-header {
  background: white;
  border-bottom: 1px solid var(--border-color);
  padding: 24px 0;
}

.header-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-title h1 {
  font-size: 28px;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0 0 4px 0;
}

.header-title p {
  font-size: 14px;
  color: var(--text-secondary);
  margin: 0;
}

.toggle-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.toggle-slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  transition: .4s;
  border-radius: 50%;
}

input:checked + .toggle-slider {
  background-color: var(--success-color);
}

input:checked + .toggle-slider:before {
  transform: translateX(20px);
}


.status-indicator.active {
  background: rgba(16, 185, 129, 0.1);
  color: var(--success-color);
}

.status-indicator.inactive {
  background: rgba(107, 114, 128, 0.1);
  color: var(--secondary-color);
}

/* Navigation Tabs */
.control-navigation {
  background: white;
  border-bottom: 1px solid var(--border-color);
}

.nav-tabs {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
  display: flex;
  gap: 8px;
}

.nav-tab {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 20px;
  background: none;
  border: none;
  color: var(--text-secondary);
  font-weight: 500;
  cursor: pointer;
  border-radius: 8px 8px 0 0;
  transition: all 0.2s;
  border-bottom: 2px solid transparent;
}

.nav-tab:hover {
  color: var(--text-primary);
  background: var(--background-color);
}

.nav-tab.active {
  color: var(--primary-color);
  border-bottom-color: var(--primary-color);
  background: rgba(16, 163, 127, 0.05);
}

/* Quick Stats */
.quick-stats {
  max-width: 1200px;
  margin: 24px auto;
  padding: 0 24px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 16px;
}

.stat-card {
  background: white;
  padding: 20px;
  border-radius: 12px;
  box-shadow: var(--shadow);
  display: flex;
  align-items: center;
  gap: 16px;
  transition: transform 0.2s, box-shadow 0.2s;
}

.stat-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}


.stat-icon {
  width: 48px;
  height: 48px;
  border-radius: 10px;
  background: rgba(16, 163, 127, 0.1);
  color: var(--primary-color);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
}

.stat-card.highlight .stat-icon {
  background: rgba(16, 163, 127, 0.15);
  color: var(--primary-color);
}

.stat-value {
  font-size: 28px;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 4px;
}

.stat-label {
  font-size: 14px;
  color: var(--text-secondary);
  font-weight: 500;
}

/* Main Content */
.control-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px 24px;
}

/* Filters and Controls */
.control-filters {
  background: white;
  padding: 20px;
  border-radius: 12px;
  box-shadow: var(--shadow);
  margin-bottom: 24px;
  display: flex;
  align-items: end;
  gap: 20px;
  flex-wrap: wrap;
}

.filter-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
  flex: 1;
  min-width: 200px;
}

.filter-group label {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
}

.filter-select {
  padding: 10px 12px;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  background: white;
  color: var(--text-primary);
  font-size: 14px;
  transition: border-color 0.2s;
}

.filter-select:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(16, 163, 127, 0.1);
}

.control-buttons {
  display: flex;
  gap: 8px;
  align-items: center;
}

.btn {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 16px;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s;
}

.btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.btn-primary {
  background: var(--primary-color);
  color: white;
}

.btn-primary:hover:not(:disabled) {
  background: var(--primary-dark);
}

.btn-warning {
  background: var(--warning-color);
  color: white;
}

.btn-warning:hover:not(:disabled) {
  background: #e68a09;
}

.btn-success {
  background: var(--success-color);
  color: white;
}

.btn-success:hover:not(:disabled) {
  background: #0da271;
}

.btn-secondary {
  background: var(--background-color);
  color: var(--text-primary);
  border: 1px solid var(--border-color);
}

.btn-secondary:hover:not(:disabled) {
  background: #e2e8f0;
}

.btn-text {
  background: none;
  border: none;
  color: var(--primary-color);
  font-weight: 500;
  cursor: pointer;
}

.btn-icon {
  width: 36px;
  height: 36px;
  border: none;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-icon.primary {
  background: rgba(16, 163, 127, 0.1);
  color: var(--primary-color);
}

.btn-icon.primary:hover {
  background: rgba(16, 163, 127, 0.2);
}

.btn-icon.warning {
  background: rgba(245, 158, 11, 0.1);
  color: var(--warning-color);
}

.btn-icon.warning:hover {
  background: rgba(245, 158, 11, 0.2);
}

/* Current Call Banner */
.current-call-banner {
  background: linear-gradient(135deg, var(--primary-color), var(--primary-dark));
  color: white;
  padding: 20px;
  border-radius: 12px;
  margin-bottom: 24px;
  box-shadow: var(--shadow-lg);
}

.call-content {
  display: flex;
  align-items: center;
  gap: 16px;
}

.call-icon {
  font-size: 32px;
  opacity: 0.9;
}

.call-details {
  flex: 1;
}

.ticket-number-large {
  font-size: 32px;
  font-weight: 700;
  margin-bottom: 4px;
}

.call-meta {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 14px;
  opacity: 0.9;
}

.priority-tag {
  padding: 4px 8px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 600;
  color: white;
}

.call-time {
  font-size: 14px;
  opacity: 0.8;
}

/* Section Headers */
.section-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 16px;
}

.section-header h3 {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.queue-info {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 14px;
  color: var(--text-secondary);
}

.queue-count {
  font-weight: 600;
  color: var(--text-primary);
}

.department-name {
  background: rgba(16, 163, 127, 0.1);
  color: var(--primary-color);
  padding: 4px 8px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 600;
}

/* Queue List */
.queue-management,
.counters-view,
.activity-view {
  background: white;
  padding: 24px;
  border-radius: 12px;
  box-shadow: var(--shadow);
}

.queue-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.queue-item {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 16px;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  transition: all 0.2s;
}

.queue-item:hover {
  border-color: var(--primary-color);
  box-shadow: var(--shadow);
}

.item-main {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 16px;
}

.ticket-header {
  display: flex;
  align-items: center;
  gap: 8px;
}

.ticket-number {
  font-size: 18px;
  font-weight: 700;
  color: var(--text-primary);
}

.position-badge {
  background: var(--background-color);
  color: var(--text-secondary);
  padding: 4px 8px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 600;
}

.ticket-details {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.patient-info {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.patient-name {
  font-weight: 600;
  color: var(--text-primary);
}

.ticket-time {
  font-size: 12px;
  color: var(--text-secondary);
}

.priority-badge {
  padding: 4px 8px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
}

.item-actions {
  display: flex;
  gap: 4px;
}

/* Empty States */
.empty-state {
  text-align: center;
  padding: 40px 20px;
  color: var(--text-secondary);
}

.empty-state i {
  font-size: 48px;
  margin-bottom: 16px;
  opacity: 0.5;
}

.empty-state h4 {
  font-size: 18px;
  margin: 0 0 8px 0;
  color: var(--text-primary);
}

.empty-state p {
  margin: 0;
  font-size: 14px;
}

/* Counters View */
.counters-summary {
  display: flex;
  align-items: center;
  gap: 16px;
  font-size: 14px;
}

.summary-item {
  display: flex;
  align-items: center;
  gap: 6px;
}

.dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
}

.dot.active {
  background: var(--success-color);
}

.dot.busy {
  background: var(--warning-color);
}

.dot.offline {
  background: var(--secondary-color);
}

.counters-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 16px;
}

.counter-card {
  background: var(--background-color);
  padding: 16px;
  border-radius: 8px;
  border: 1px solid var(--border-color);
  transition: all 0.2s;
}

.counter-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow);
}

.counter-card.active {
  border-color: var(--success-color);
}

.counter-card.busy {
  border-color: var(--warning-color);
}

.counter-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 8px;
}

.counter-number {
  font-weight: 600;
  color: var(--text-primary);
}

.counter-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 8px;
}

.counter-department {
  font-size: 12px;
  color: var(--text-secondary);
}

.counter-status {
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
}

.current-ticket {
  background: white;
  padding: 8px;
  border-radius: 6px;
  text-align: center;
  border: 1px solid var(--border-color);
}

.ticket-number-small {
  font-weight: 700;
  color: var(--primary-color);
  font-size: 14px;
}

/* Activity View */
.activity-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.activity-item {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  padding: 12px;
  border-radius: 8px;
  border: 1px solid var(--border-color);
  transition: all 0.2s;
}

.activity-item:hover {
  border-color: var(--primary-color);
}

.activity-item.success {
  border-left: 4px solid var(--success-color);
}

.activity-item.error {
  border-left: 4px solid var(--error-color);
}

.activity-item.warning {
  border-left: 4px solid var(--warning-color);
}

.activity-item.info {
  border-left: 4px solid var(--info-color);
}

.activity-icon {
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  margin-top: 2px;
}

.activity-item.success .activity-icon {
  color: var(--success-color);
}

.activity-item.error .activity-icon {
  color: var(--error-color);
}

.activity-item.warning .activity-icon {
  color: var(--warning-color);
}

.activity-item.info .activity-icon {
  color: var(--info-color);
}

.activity-content {
  flex: 1;
}

.activity-message {
  font-size: 14px;
  color: var(--text-primary);
  margin-bottom: 2px;
}

.activity-time {
  font-size: 12px;
  color: var(--text-secondary);
}

/* Responsive Design */
@media (max-width: 768px) {
 .header-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
  display: flex;
  align-items: center;
  justify-content: center; /* Changed from space-between to center */
}
  
  .control-filters {
    flex-direction: column;
    align-items: stretch;
  }
  
  .filter-group {
    min-width: auto;
  }
  
  .control-buttons {
    justify-content: space-between;
  }
  
  .quick-stats {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .call-content {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }
  
  .section-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }
  
  .queue-item {
    flex-direction: column;
    align-items: stretch;
    gap: 12px;
  }
  
  .item-main {
    flex-direction: column;
    align-items: stretch;
    gap: 12px;
  }
  
  .ticket-details {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }
}