/* TicketDispenser.css - Updated to match AdminDashboard styling */

:root {
  --primary-color: #10a37f;
  --primary-dark: #0d8c6c;
  --secondary-color: #5436da;
  --background-color: #f8f9fa;
  --sidebar-bg: #ffffff;
  --card-bg: #ffffff;
  --text-primary: #1a1a1a;
  --text-secondary: #6b7280;
  --text-light: #9ca3af;
  --border-color: #e5e7eb;
  --success-color: #10b981;
  --warning-color: #f59e0b;
  --danger-color: #ef4444;
  --info-color: #3b82f6;
  --shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.dispenser-container {
  min-height: 100vh;
  background: var(--background-color);
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
}

/* Loading State */
.dispenser-container.loading {
  display: flex;
  align-items: center;
  justify-content: center;
}

.loading-spinner {
  text-align: center;
  color: var(--text-secondary);
}

.spinner {
  width: 40px;
  height: 40px;
  border: 4px solid rgba(16, 163, 127, 0.2);
  border-left: 4px solid var(--primary-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 20px;
}

.loading-text {
  font-size: 16px;
  font-weight: 500;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Header */
.hospital-header {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 30px;
  background: var(--card-bg);
  padding: 24px;
  border-radius: 12px;
  box-shadow: var(--shadow);
  border: 1px solid var(--border-color);
}

.hospital-logo {
  width: 60px;
  height: 60px;
  background: var(--primary-color);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 28px;
}

.hospital-info h1 {
  margin: 0;
  color: var(--text-primary);
  font-size: 28px;
  font-weight: 700;
}

.subtitle {
  margin: 4px 0 0 0;
  color: var(--text-secondary);
  font-size: 16px;
  font-weight: 500;
}

/* Welcome Card */
.welcome-card {
  background: var(--card-bg);
  padding: 24px;
  border-radius: 12px;
  box-shadow: var(--shadow);
  margin-bottom: 24px;
  display: flex;
  align-items: center;
  gap: 20px;
  border-left: 4px solid var(--primary-color);
}

.welcome-icon {
  width: 60px;
  height: 60px;
  background: rgba(16, 163, 127, 0.1);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--primary-color);
  font-size: 24px;
}

.welcome-content h2 {
  margin: 0 0 8px 0;
  color: var(--text-primary);
  font-size: 20px;
  font-weight: 600;
}

.welcome-content p {
  margin: 0;
  color: var(--text-secondary);
  font-size: 14px;
}

/* Status Cards */
.status-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 16px;
  margin-bottom: 24px;
}

.status-card {
  background: var(--card-bg);
  padding: 20px;
  border-radius: 12px;
  box-shadow: var(--shadow);
  display: flex;
  align-items: center;
  gap: 16px;
  transition: transform 0.2s, box-shadow 0.2s;
}

.status-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.status-icon {
  width: 50px;
  height: 50px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  color: white;
}

.status-icon.primary {
  background: var(--primary-color);
}

.status-icon.secondary {
  background: var(--secondary-color);
}

.status-number {
  font-size: 24px;
  font-weight: 700;
  color: var(--text-primary);
  line-height: 1;
}

.status-label {
  font-size: 14px;
  color: var(--text-secondary);
  margin-top: 4px;
}

/* Patient Info Card */
.patient-info-card {
  background: var(--card-bg);
  padding: 24px;
  border-radius: 12px;
  box-shadow: var(--shadow);
  margin-bottom: 32px;
}

.card-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 20px;
}

.card-header h3 {
  margin: 0;
  color: var(--text-primary);
  font-size: 18px;
  font-weight: 600;
}

.card-header i {
  color: var(--primary-color);
  font-size: 18px;
}

.optional-badge {
  background: var(--background-color);
  color: var(--text-secondary);
  padding: 4px 8px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 500;
}

.form-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
}

.input-group label {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;
  color: var(--text-primary);
  font-weight: 500;
  font-size: 14px;
}

.input-group input {
  width: 100%;
  padding: 12px 16px;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  font-size: 14px;
  transition: all 0.2s;
  background: var(--background-color);
}

.input-group input:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(16, 163, 127, 0.1);
}

/* Departments Section */
.departments-section {
  margin-top: 32px;
}

.section-header {
  margin-bottom: 24px;
}

.section-header h3 {
  margin: 0 0 8px 0;
  color: var(--text-primary);
  font-size: 20px;
  font-weight: 600;
}

.section-header p {
  margin: 0;
  color: var(--text-secondary);
  font-size: 14px;
}

.department-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 20px;
}

.department-card {
  background: var(--card-bg);
  padding: 20px;
  border-radius: 12px;
  box-shadow: var(--shadow);
  transition: all 0.3s ease;
  border: 1px solid var(--border-color);
}

.department-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.department-card .card-header {
  display: flex;
  align-items: flex-start;
  gap: 16px;
  margin-bottom: 16px;
}

.dept-icon {
  width: 45px;
  height: 45px;
  background: rgba(16, 163, 127, 0.1);
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--primary-color);
  font-size: 18px;
}

.dept-title h4 {
  margin: 0 0 4px 0;
  color: var(--text-primary);
  font-size: 16px;
  font-weight: 600;
}

.dept-status {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 12px;
  font-weight: 500;
}

.dept-status.available {
  color: var(--success-color);
}

.dept-status i {
  font-size: 8px;
}

.dept-metrics {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
  margin-bottom: 16px;
}

.metric {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 12px;
  background: var(--background-color);
  border-radius: 8px;
}

.metric-icon {
  color: var(--primary-color);
  font-size: 14px;
}

.metric-value {
  font-size: 16px;
  font-weight: 700;
  color: var(--text-primary);
  line-height: 1;
}

.metric-label {
  font-size: 12px;
  color: var(--text-secondary);
  margin-top: 2px;
}

.counters-list {
  margin-bottom: 16px;
}

.counters-label {
  font-size: 12px;
  color: var(--text-secondary);
  margin-bottom: 8px;
  font-weight: 500;
}

.counter-badges {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}

.counter-badge {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 4px 8px;
  background: var(--background-color);
  border-radius: 6px;
  font-size: 11px;
  font-weight: 500;
  color: var(--text-primary);
  border: 1px solid var(--border-color);
}

.counter-badge.busy {
  background: rgba(239, 68, 68, 0.1);
  color: var(--danger-color);
  border-color: rgba(239, 68, 68, 0.2);
}

.busy-indicator {
  width: 6px;
  height: 6px;
  background: var(--danger-color);
  border-radius: 50%;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

.ticket-button {
  width: 100%;
  padding: 12px 16px;
  background: var(--primary-color);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  cursor: pointer;
  transition: all 0.2s;
}

.ticket-button:hover {
  background: var(--primary-dark);
  transform: translateY(-1px);
}

/* Empty State */
.empty-state {
  background: var(--card-bg);
  padding: 40px 24px;
  border-radius: 12px;
  box-shadow: var(--shadow);
  text-align: center;
}

.empty-icon {
  width: 60px;
  height: 60px;
  background: rgba(239, 68, 68, 0.1);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--danger-color);
  font-size: 24px;
  margin: 0 auto 16px;
}

.empty-state h3 {
  margin: 0 0 12px 0;
  color: var(--text-primary);
  font-size: 18px;
  font-weight: 600;
}

.empty-state p {
  margin: 0 0 8px 0;
  color: var(--text-secondary);
  font-size: 14px;
}

.retry-button {
  margin-top: 16px;
  padding: 10px 20px;
  background: var(--primary-color);
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s;
  display: flex;
  align-items: center;
  gap: 6px;
  margin: 16px auto 0;
}

.retry-button:hover {
  background: var(--primary-dark);
}

/* Ticket View */
.ticket-view {
  max-width: 600px;
  margin: 0 auto;
}

.ticket-success-header {
  background: var(--card-bg);
  padding: 24px;
  border-radius: 12px;
  box-shadow: var(--shadow);
  margin-bottom: 24px;
  display: flex;
  align-items: center;
  gap: 16px;
}

.success-icon {
  width: 60px;
  height: 60px;
  background: rgba(16, 185, 129, 0.1);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--success-color);
  font-size: 24px;
}

.success-content h2 {
  margin: 0 0 8px 0;
  color: var(--text-primary);
  font-size: 20px;
  font-weight: 600;
}

.success-content p {
  margin: 0;
  color: var(--text-secondary);
  font-size: 14px;
}

.ticket-card {
  background: var(--card-bg);
  border-radius: 12px;
  box-shadow: var(--shadow-lg);
  overflow: hidden;
  margin-bottom: 24px;
  border: 1px solid var(--border-color);
}

.ticket-header {
  background: var(--primary-color);
  padding: 20px;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.hospital-brand {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 600;
  font-size: 16px;
}

.ticket-type {
  background: rgba(255, 255, 255, 0.2);
  padding: 4px 8px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 500;
}

.ticket-number-section {
  padding: 32px 20px;
  text-align: center;
  background: var(--background-color);
}

.ticket-label {
  font-size: 14px;
  color: var(--text-secondary);
  margin-bottom: 8px;
  font-weight: 500;
}

.ticket-number {
  font-size: 36px;
  font-weight: 800;
  color: var(--text-primary);
  letter-spacing: 2px;
}

.ticket-details-grid {
  padding: 20px;
  display: grid;
  gap: 12px;
}

.detail-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 0;
  border-bottom: 1px solid var(--border-color);
}

.detail-item:last-child {
  border-bottom: none;
}

.detail-label {
  display: flex;
  align-items: center;
  gap: 8px;
  color: var(--text-secondary);
  font-weight: 500;
  font-size: 14px;
}

.detail-value {
  color: var(--text-primary);
  font-weight: 600;
  font-size: 14px;
}

.wait-time-card {
  margin: 0 20px 20px;
  padding: 16px;
  background: rgba(245, 158, 11, 0.1);
  border-radius: 8px;
  display: flex;
  align-items: center;
  gap: 12px;
  border-left: 4px solid var(--warning-color);
}

.wait-icon {
  width: 40px;
  height: 40px;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--warning-color);
  font-size: 16px;
}

.wait-time {
  font-size: 20px;
  font-weight: 700;
  color: var(--text-primary);
  line-height: 1;
}

.wait-label {
  font-size: 12px;
  color: var(--text-secondary);
  margin-top: 2px;
}

.instructions-card {
  padding: 20px;
  background: var(--background-color);
  margin: 0 20px 20px;
  border-radius: 8px;
}

.instructions-card h4 {
  margin: 0 0 12px 0;
  color: var(--text-primary);
  font-size: 14px;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 8px;
}

.instructions-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.instructions-list li {
  padding: 6px 0;
  display: flex;
  align-items: flex-start;
  gap: 10px;
  color: var(--text-primary);
  font-size: 13px;
}

.instructions-list li i {
  color: var(--primary-color);
  margin-top: 2px;
  flex-shrink: 0;
  font-size: 12px;
}

.ticket-actions {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
}

.action-button {
  padding: 12px 16px;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
}

.action-button.primary {
  background: var(--primary-color);
  color: white;
}

.action-button.secondary {
  background: var(--background-color);
  color: var(--text-primary);
  border: 1px solid var(--border-color);
}

.action-button:hover {
  transform: translateY(-1px);
  box-shadow: var(--shadow);
}

.action-button.primary:hover {
  background: var(--primary-dark);
}

.action-button.secondary:hover {
  background: #f1f5f9;
}

/* Responsive Design */
@media (max-width: 768px) {
  .dispenser-container {
    padding: 16px;
  }
  
  .hospital-header {
    flex-direction: column;
    text-align: center;
    gap: 12px;
  }
  
  .form-grid {
    grid-template-columns: 1fr;
  }
  
  .department-grid {
    grid-template-columns: 1fr;
  }
  
  .status-cards {
    grid-template-columns: 1fr;
  }
  
  .ticket-actions {
    grid-template-columns: 1fr;
  }
  
  .ticket-number {
    font-size: 28px;
  }
  
  .welcome-card {
    flex-direction: column;
    text-align: center;
  }
}