.login-container {
  position: relative;
  display: flex;
  height: 100vh;
  width: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #f5f8f8;
  padding: 1rem;
  overflow: hidden;
  font-family: 'Inter', sans-serif;
}

.dark .login-container {
  background-color: #0f231e;
}

.background-shapes {
  position: absolute;
  inset: 0;
  z-index: 0;
}

.background-shapes::before {
  content: '';
  position: absolute;
  height: 100%;
  width: 100%;
  background: linear-gradient(to bottom right, rgb(204, 251, 241), rgb(191, 219, 254));
}

.dark .background-shapes::before {
  background: linear-gradient(to bottom right, rgba(20, 184, 166, 0.5), rgba(59, 130, 246, 0.5));
}

.shape {
  position: absolute;
  border-radius: 50%;
  background: rgba(5, 179, 138, 0.1);
  animation: float 6s ease-in-out infinite;
}

.shape-1 {
  width: 200px;
  height: 200px;
  top: 10%;
  left: 10%;
  animation-delay: 0s;
}

.shape-2 {
  width: 150px;
  height: 150px;
  top: 60%;
  left: 80%;
  animation-delay: 2s;
}

.shape-3 {
  width: 100px;
  height: 100px;
  top: 20%;
  left: 80%;
  animation-delay: 4s;
}

.shape-4 {
  width: 120px;
  height: 120px;
  top: 70%;
  left: 15%;
  animation-delay: 1s;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0) rotate(0deg);
  }
  50% {
    transform: translateY(-20px) rotate(180deg);
  }
}

.grid {
  display: grid;
}

.grid-cols-1 {
